      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      text-transform: uppercase;
      color: #7E7E82; }
      @media (min-width: 768px) {
        .prices_body .header_main .title_main {
          margin-left: 24px; } }
      @media (min-width: 1120px) {
