  .prices_body .text--price {
    max-width: 288px;
    height: -moz-fit-content;
    height: fit-content;
    margin: 0px 16px 16px 16px;
    font-family: 'TTLakesRegular';
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #1B1C21; }
