  .header .header_container .header_logo .header_logo--chat {
    display: none;
    margin-left: 16px;
    height: 40px;
    width: 40px;
    background-image: url(./static/chat.svg); }
    @media (min-width: 512px) {
      .header .header_container .header_logo .header_logo--chat {
        display: block; } }
  .header .header_container .header_logo .header_logo--profile {
    display: none;
    margin-left: 16px;
    height: 40px;
    width: 40px;
    background-image: url(./static/profile.svg); }
    @media (min-width: 558px) {
      .header .header_container .header_logo .header_logo--profile {
        display: block; } }
  .header .header_container .header_divider {
    height: 32px;
    width: 2px;
    border-radius: 1px;
    background-color: #EAEAEA; }
    @media (min-width: 390px) {
      .header .header_container .header_divider {
