body {
  margin: 0;
  padding: 0;
  font-family: TTLakesRegular; }

button {
  border: none;
  background: none;
  margin: 0%;
  padding: 0%; }

li {
  list-style-type: none; }

a {
  text-decoration: none;
  color: #7E7E82; }

.article {
  display: flex;
  flex-wrap: wrap;
  min-width: 320px; }
  @media (min-width: 705px) {
    .article {
      flex-wrap: nowrap; } }
  .article .article_content {
    display: flex;
    flex-wrap: wrap;
    margin-right: auto;
    flex-direction: column; }
    .article .article_content .article_text-full {
      margin: 0;
      display: flex;
      height: 90px;
      overflow: hidden;
      font-family: 'TTLakesRegular';
      font-size: 14px;
      line-height: 18px;
      letter-spacing: 0.2px;
      color: #1B1C21;
      padding: 0 16px;
      margin-bottom: 16px; }
      @media (min-width: 705px) {
        .article .article_content .article_text-full {
          max-width: 417px; } }
      @media (min-width: 768px) {
        .article .article_content .article_text-full {
          max-width: 957px;
          height: 160px;
          padding: 0 24px; } }
    .article .article_content .article_text--show {
      height: -moz-fit-content;
      height: fit-content; }
    .article .article_content .article_readmore::before {
      content: url(./static/expand.svg);
      background: content no-repeat; }
    .article .article_content .article_readmore {
      font-family: 'TTLakesMedium';
      color: black;
      font-size: 16px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      padding-left: 20px;
      margin-bottom: 16px; }
    .article .article_content .article_readless--hide {
      display: none; }
  .article .article_picture {
    display: block;
    min-width: 320px;
    height: 176px;
    margin-right: auto;
    margin-left: auto;
    background: no-repeat url(./static/img-phone.jpg); }
    @media (min-width: 705px) {
      .article .article_picture {
        margin: 0 24px; } }
    @media (min-width: 768px) {
      .article .article_picture {
        display: flex;
        min-width: 360px;
        height: 208px;
        background-image: url(./static/img-tablet.jpg); } }
    @media (min-width: 1120px) {
      .article .article_picture {
        min-width: 532px;
        height: 307px;
        background-image: url(./static/img-big.jpg); } }

.popup {
  position: fixed;
  z-index: 10;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  left: 0; }

.main_container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  min-width: 320px;
  padding-bottom: 16px;
  background-color: #F8F8F8;
  margin: 0px auto; }
  @media (min-width: 1366px) {
    .main_container {
      min-width: 1120px; } }

.title::before {
  content: " ";
  position: absolute;
  left: 0%;
  width: 4px;
  height: 40px;
  background: #41F6D7;
  border-top-right-radius: 45px;
  border-bottom-right-radius: 45px; }

.title {
  display: flex;
  margin-left: 16px;
  font-family: TTLakesBold; }
  @media (min-width: 768px) {
    .title {
      margin-left: 24px; } }
  @media (min-width: 1366px) {
    .title {
      margin-right: auto; } }

.nav-scroller {
  overflow-y: hidden;
  margin: 16px 0;
  display: flex;
  flex-wrap: wrap;
  padding-left: 16px; }
  @media (min-width: 768px) {
    .nav-scroller {
      padding-left: 24px; } }
  @media (min-width: 1366px) {
    .nav-scroller {
      width: 90%; } }
  .nav-scroller .nav-scroller__items {
    display: flex;
    overflow-x: auto;
    white-space: nowrap;
    width: 1365px; }
    @media (min-width: 1366px) {
      .nav-scroller .nav-scroller__items {
        flex-wrap: wrap;
        flex-grow: 1; } }
    .nav-scroller .nav-scroller__items .nav-scroller__item {
      display: flex;
      padding: 0 8px;
      height: 40px;
      align-items: center;
      text-decoration: none;
      font-family: TTLakesMedium;
      margin-right: auto; }
    .nav-scroller .nav-scroller__items .nav-scroller__item--pc-link {
      display: none; }
      @media (min-width: 1366px) {
        .nav-scroller .nav-scroller__items .nav-scroller__item--pc-link {
          display: flex; } }
    .nav-scroller .nav-scroller__items .nav-scroller__item:nth-child(2) {
      background-color: #FFFFFF;
      border: 2px solid #B8FFEC;
      border-radius: 6px; }
    @media (min-width: 1366px) {
      .nav-scroller .nav-scroller__items .nav-scroller__item:first-child {
        background-color: #FFFFFF;
        border: 2px solid #B8FFEC;
        border-radius: 6px; } }
    @media (min-width: 1366px) {
      .nav-scroller .nav-scroller__items .nav-scroller__item:nth-child(2) {
        background: none;
        border: none; } }

.header-pc {
  display: none; }
  @media (min-width: 1366px) {
    .header-pc {
      display: flex;
      flex-wrap: wrap;
      margin-left: auto;
      justify-content: flex-end;
      align-items: center;
      flex-grow: 1;
      margin-right: 24px; } }
  @media (min-width: 1366px) {
    .header-pc .header-pc_button {
      font-size: 16px;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      margin-left: 8px;
      margin-left: 32px;
      font-family: TTLakesMedium; } }
  @media (min-width: 1366px) {
    .header-pc .header-pc_button--request::after {
      content: url(./static/repair.svg);
      background: content no-repeat;
      margin-left: 9px; } }
  @media (min-width: 1366px) {
    .header-pc .header-pc_button--chesk::after {
      content: url(./static/checkstatus.svg);
      background: content no-repeat;
      margin-left: 9px; } }

.article_readless--show::before {
  content: url(./static/expand.svg);
  background: content no-repeat;
  transform: rotate(180deg); }

.article_readless--show {
  color: black;
  font-size: 16px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-left: 20px;
  margin-bottom: 16px; }
