.sidenav {
  height: 100%;
  display: block;
  max-width: 360px;
  background-color: white;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2);
  overflow-y: scroll; }
  @media (min-width: 361px) {
    .sidenav {
      width: 320px; } }
  @media (min-width: 1366px) {
    .sidenav {
      display: block;
      height: auto;
      position: fixed;
      box-shadow: none; } }
  .sidenav .sidenav_header {
    display: flex;
    flex-wrap: wrap;
    max-width: 360px;
    height: 52px;
    justify-content: space-evenly;
    align-items: center;
    padding-top: 18px; }
    @media (min-width: 361px) {
      .sidenav .sidenav_header {
        width: 320px; } }
    @media (min-width: 1366px) {
      .sidenav .sidenav_header {
        height: 68px; } }
    .sidenav .sidenav_header .sidenav_header--burger {
      height: 40px;
      width: 40px;
      margin-left: 24px;
      background-image: url(./static/burger_1.svg); }
      @media (min-width: 1366px) {
        .sidenav .sidenav_header .sidenav_header--burger {
          display: none; } }
    .sidenav .sidenav_header .sidenav_header--logo {
      margin: auto;
      margin-left: 17px; }
    .sidenav .sidenav_header .sidenav_header--search {
      margin-right: 24px;
      height: 40px;
      width: 40px;
      background-image: url(./static/search.svg); }
  .sidenav .sidenav_main {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    width: 320px; }
    @media (min-width: 1366px) {
      .sidenav .sidenav_main .navigation {
        position: relative; } }
    .sidenav .sidenav_main .navigation .navigation_menu {
      list-style: none;
      margin: 0;
      padding: 0; }
      .sidenav .sidenav_main .navigation .navigation_menu .navigation_menu--link {
        height: 24px;
        margin: 16px auto 24px 16px;
        font-family: TTLakesMedium;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;
        color: #1B1C21; }
      .sidenav .sidenav_main .navigation .navigation_menu .navigation_menu--link--active {
        margin-top: 34px;
        color: #7E7E82; }
    .sidenav .sidenav_main .navigation .navigation_menu::before {
      content: " ";
      position: absolute;
      top: 30px;
      width: 4px;
      height: 32px;
      background: #41F6D7;
      border-top-right-radius: 45px;
      border-bottom-right-radius: 45px; }
  .sidenav .sidenav_footer {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: flex-start;
    width: 320px;
    height: 236px; }
    .sidenav .sidenav_footer .footer_button {
      display: flex;
      margin-left: 24px; }
      .sidenav .sidenav_footer .footer_button .footer_button--call {
        height: 40px;
        width: 40px;
        background-image: url(./static/call.svg); }
      .sidenav .sidenav_footer .footer_button .footer_button--chat {
        margin: auto 16px;
        height: 40px;
        width: 40px;
        background-image: url(./static/chat.svg); }
      .sidenav .sidenav_footer .footer_button .footer_button--profile {
        margin: auto;
        height: 40px;
        width: 40px;
        background-image: url(./static/profile.svg); }
    .sidenav .sidenav_footer .footer_contact {
      display: flex;
      flex-wrap: wrap;
      flex-direction: column;
      margin-left: 24px;
      margin-top: 16px; }
      .sidenav .sidenav_footer .footer_contact .email {
        margin-bottom: 12px;
        font-family: 'TTLakesMedium';
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;
        color: #1B1C21; }
      .sidenav .sidenav_footer .footer_contact .tel {
        font-family: 'TTLakesBold';
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.6px;
        color: #1B1C21; }
    .sidenav .sidenav_footer .footer_language {
      display: flex;
      flex-wrap: wrap;
      margin-left: 24px;
      margin-top: 40px; }
      .sidenav .sidenav_footer .footer_language .language-menu {
        padding: 0;
        margin: 0;
        list-style: none;
        margin-right: 7px;
        font-family: 'TTLakesMedium';
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px;
        color: #1B1C21; }
      .sidenav .sidenav_footer .footer_language .language-menu--active {
        color: #7E7E82; }

.sidenav_hide {
  display: none; }
  @media (min-width: 1366px) {
    .sidenav_hide {
      display: block; } }

@keyframes scale-rotate {
  from {
    transform: scale(1.5); }
  50% {
    transform: scale(1.5) rotate(360deg); } }

.sidenav_header--burger:hover {
  border-radius: 50%;
  animation-name: scale-rotate;
  animation-duration: 1.5s;
  box-shadow: 0px 0px 5px 5px #7E7E82; }

.sidenav_header--search:hover {
  border-radius: 50%;
  animation-name: scale-rotate;
  animation-duration: 1.5s;
  box-shadow: 0px 0px 5px 5px #7E7E82; }

.footer_button--call:hover {
  border-radius: 50%;
  animation-name: scale-rotate;
  animation-duration: 1.5s;
  box-shadow: 0px 0px 5px 5px #7E7E82; }

.footer_button--chat:hover {
  border-radius: 50%;
  animation-name: scale-rotate;
  animation-duration: 1.5s;
  box-shadow: 0px 0px 5px 5px #7E7E82; }

.footer_button--profile:hover {
  border-radius: 50%;
  animation-name: scale-rotate;
  animation-duration: 1.5s;
  box-shadow: 0px 0px 5px 5px #7E7E82; }

