.footer {
  display: flex;
  flex-wrap: wrap;
  background: #F8F8F8;
  border-top: 1px solid #D9FFF5;
  align-items: center;
  min-width: 320px; }
  @media (min-width: 768px) {
    .footer {
      height: 105px; } }
  @media (min-width: 1120px) {
    .footer {
      min-width: 1120px;
      height: 85px; } }
  .footer .footer_menu {
    align-self: flex-start;
