@font-face {
  font-family: 'TTLakesBold';
  src: url(./fonts/TTLakes-Bold.ttf), url(./fonts/TTLakes-Bold.eot) format("eot"), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  font-weight: 700;
  font-style: normal; }

.repair_body {
  background-color: #F8F8F8;
  min-width: 320px;
  min-height: 308px;
  overflow: hidden; }
  @media (min-width: 768px) {
    .repair_body {
      min-height: 322px; } }
  @media (min-width: 1120px) {
    .repair_body {
      min-width: 1120px; } }
  .repair_body .header_main {
    display: flex;
    flex-wrap: wrap;
    min-width: 320px;
    height: 80px;
    background: linear-gradient(to right, #F8F8F8, #FFFFFF); }
    @media (min-width: 768px) {
      .repair_body .header_main {
        height: 56px; } }
    .repair_body .header_main .title_main {
      margin: 0;
      margin-left: 16px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      font-family: TTLakesRegular;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      text-transform: uppercase;
      color: #7E7E82; }
      @media (min-width: 768px) {
        .repair_body .header_main .title_main {
          margin-left: 24px; } }
      @media (min-width: 1120px) {
        .repair_body .header_main .title_main {
          margin: 0;
          margin-left: 32px; } }
  .repair_body .repair {
    display: flex;
    flex-wrap: wrap;
    /*.button_show_repair {
            display: none;
        }*/ }
    .repair_body .repair .repair_list {
      display: flex;
      padding: 0;
      margin-bottom: 0;
      flex-grow: 1; }
      @media (min-width: 768px) {
        .repair_body .repair .repair_list {
          flex-wrap: wrap;
          margin-top: 24px; } }
      .repair_body .repair .repair_list .repair_list-item {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 240px;
        height: 160px;
        border: 1px solid #EAEAEA;
        border-radius: 6px;
        margin-left: 16px;
        margin-right: auto;
        margin-bottom: 16px;
        background-color: #FFFFFF; }
        @media (min-width: 768px) {
          .repair_body .repair .repair_list .repair_list-item {
            width: 224px;
            margin-left: 24px; } }
        @media (min-width: 1120px) {
          .repair_body .repair .repair_list .repair_list-item {
            width: 240px;
            margin-left: 32px; } }
        @media (min-width: 1366px) {
          .repair_body .repair .repair_list .repair_list-item {
            margin-left: 19px; } }
        .repair_body .repair .repair_list .repair_list-item .repair_text {
          margin-left: 16px;
          margin-top: 16px;
          align-self: flex-start;
          font-family: 'TTLakesMedium';
          font-size: 16px;
          line-height: 24px;
          letter-spacing: -0.2px;
          color: #1B1C21; }
        .repair_body .repair .repair_list .repair_list-item .brands_button {
          background-image: url(./static/go.svg);
          margin-left: auto;
          margin-right: 16px;
          margin-bottom: 16px;
          align-self: flex-end;
          width: 40px;
          height: 40px; }
      .repair_body .repair .repair_list .hide_repair {
        display: none; }
      @media (min-width: 768px) {
        .repair_body .repair .repair_list .hide_tablet_repair {
          display: none; } }
      @media (min-width: 1000px) {
        .repair_body .repair .repair_list .hide_tablet_repair {
          display: flex; } }
      @media (min-width: 768px) {
        .repair_body .repair .repair_list .hide_pc_repair {
          display: none; } }
      @media (min-width: 1625px) {
        .repair_body .repair .repair_list .hide_pc_repair {
          display: flex; } }
    .repair_body .repair .button_more_repair {
      display: none;
      font-family: 'TTLakesMedium';
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #1B1C21;
      margin-left: 24px;
      margin-bottom: 31px; }
      @media (min-width: 768px) {
        .repair_body .repair .button_more_repair {
          display: inline-block; } }
      @media (min-width: 1120px) {
        .repair_body .repair .button_more_repair {
          margin-left: 32px; } }
    .repair_body .repair .button_hide_repair {
      margin-top: 17px;
      margin-bottom: 14px; }
    .repair_body .repair .button_show_repair::before {
      display: inline-block;
      content: url(./static/expand.png);
      transform: translateY(20%); }
    .repair_body .repair .button_hide_repair::before {
      display: inline-block;
      content: url(./static/expand.png);
      transform: rotate(180deg); }
    .repair_body .repair .hide_repair {
      display: none; }

.swiper-pagination {
  position: unset; }
  @media (min-width: 768px) {
    .swiper-pagination {
      display: none; } }

.swiper-pagination-bullet {
  background: #DDDDDD; }

.swiper-pagination-bullet-active {
  background: #B5B6BC; }

