.brands_body {
  background-color: #F8F8F8;
  margin: 0;
  min-width: 320px;
  min-height: 234px;
  overflow: hidden; }
  @media (min-width: 768px) {
    .brands_body {
      min-height: 322px; } }
  @media (min-width: 1120px) {
    .brands_body {
      min-width: 1120px; } }
  .brands_body .header_main {
    display: flex;
    flex-wrap: wrap;
    min-width: 320px;
    height: 80px;
    background: linear-gradient(to right, #F8F8F8, #FFFFFF); }
    @media (min-width: 768px) {
      .brands_body .header_main {
        height: 56px; } }
    .brands_body .header_main .title_main {
      margin: 0;
      margin-left: 16px;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      font-family: TTLakesRegular;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      text-transform: uppercase;
      color: #7E7E82; }
      @media (min-width: 768px) {
        .brands_body .header_main .title_main {
          margin-left: 24px; } }
      @media (min-width: 1120px) {
        .brands_body .header_main .title_main {
          margin: 0;
          margin-left: 32px; } }
  .brands_body .brands {
    display: flex;
    flex-wrap: wrap; }
    .brands_body .brands .brands_list {
      display: flex;
      padding: 0;
      margin-bottom: 0;
      flex-grow: 1; }
      @media (min-width: 768px) {
        .brands_body .brands .brands_list {
          flex-wrap: wrap;
          margin-top: 24px;
          min-width: 768px;
          max-height: 180px;
          overflow: hidden; } }
      .brands_body .brands .brands_list .brands_list-item {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        width: 240px;
        height: 72px;
        border: 1px solid #EAEAEA;
        border-radius: 6px;
        margin-left: 16px;
        margin-right: auto;
        margin-bottom: 16px;
        background-color: #FFFFFF; }
        @media (min-width: 768px) {
          .brands_body .brands .brands_list .brands_list-item {
            width: 224px;
            margin-left: 24px; } }
        @media (min-width: 1120px) {
          .brands_body .brands .brands_list .brands_list-item {
            width: 240px;
            margin-left: 32px; } }
        @media (min-width: 1366px) {
          .brands_body .brands .brands_list .brands_list-item {
            margin-left: 19px; } }
        .brands_body .brands .brands_list .brands_list-item .brands_image {
          margin-left: 16px; }
        .brands_body .brands .brands_list .brands_list-item .brands_button {
          background-image: url(./static/go.svg);
          margin-left: auto;
          margin-right: 16px;
          width: 40px;
          height: 40px; }
      .brands_body .brands .brands_list .hide_tablet_brands {
        display: none; }
        @media (min-width: 1000px) {
          .brands_body .brands .brands_list .hide_tablet_brands {
            display: flex; } }
      .brands_body .brands .brands_list .hide_brands {
        display: none; }
        @media (min-width: 768px) {
          .brands_body .brands .brands_list .hide_brands {
            display: none; } }
      .brands_body .brands .brands_list .hide_pc_brands {
        display: none; }
        @media (min-width: 768px) {
          .brands_body .brands .brands_list .hide_pc_brands {
            display: none; } }
        @media (min-width: 1250px) {
          .brands_body .brands .brands_list .hide_pc_brands {
            display: flex; } }
        @media (min-width: 1366px) {
          .brands_body .brands .brands_list .hide_pc_brands {
            display: none; } }
        @media (min-width: 1570px) {
          .brands_body .brands .brands_list .hide_pc_brands {
            display: flex; } }
        @media (min-width: 1625px) {
          .brands_body .brands .brands_list .hide_pc_brands {
            display: flex; } }
    .brands_body .brands .show {
      max-height: unset; }
    .brands_body .brands .button_more_brands {
      display: none;
      font-family: 'TTLakesMedium';
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      color: #1B1C21;
      margin-left: 24px;
      margin-bottom: 31px; }
      @media (min-width: 768px) {
        .brands_body .brands .button_more_brands {
          display: inline-block; } }
      @media (min-width: 1120px) {
        .brands_body .brands .button_more_brands {
          margin-left: 32px; } }
    .brands_body .brands .hide_brands {
      display: none; }
      @media (min-width: 768px) {
        .brands_body .brands .hide_brands {
          display: none; } }

.swiper-pagination {
  position: unset; }
  @media (min-width: 768px) {
    .swiper-pagination {
      display: none; } }

.swiper-pagination-bullet {
  background: #DDDDDD; }

.swiper-pagination-bullet-active {
  background: #B5B6BC; }

