.popup {
  position: fixed;
  z-index: 11;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  left: 0; }

.modalCall {
  position: fixed;
  right: 0;
  height: 100%;
  background-color: #FFFFFF;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  min-height: 362px;
  width: 288px;
  padding: 24px 16px;
  box-shadow: 16px 0px 52px rgba(14, 24, 80, 0.2); }
  @media (min-width: 544px) {
    .modalCall {
      min-height: 268px;
      width: 376px;
      padding: 40px 32px; } }
  .modalCall .modalCall_burger {
    margin: 0;
    padding: 0;
    border: none;
    margin-bottom: 40px;
    background: none;
    height: 40px;
    width: 40px;
    background-image: url(./static/burger_1.svg); }
    @media (min-width: 544px) {
      .modalCall .modalCall_burger {
        position: absolute;
        top: 36px;
        left: -73px; } }
  .modalCall .modalCall_title {
    margin: 0;
    margin-bottom: 24px;
    position: relative;
    font-family: 'TTLakesBold';
    font-size: 24px;
    line-height: 32px;
    letter-spacing: -0.6px;
    color: #1B1C21; }
  .modalCall .modalCall_form {
    display: flex;
    flex-wrap: wrap;
    height: 64px; }
    .modalCall .modalCall_form .modalCall_form--field {
      margin-bottom: 16px;
      width: 270px;
      height: 48px;
      background: #FFFFFF;
      border: 1px solid #EAEAEA;
      border-radius: 8px;
      font-family: 'TTLakesRegular';
      font-size: 16px;
      line-height: 24px;
      color: #B5B6BC;
      padding-left: 16px; }
      @media (min-width: 544px) {
        .modalCall .modalCall_form .modalCall_form--field {
          width: 358px; } }
    .modalCall .modalCall_form .modalCall_button {
      align-self: flex-end;
      position: absolute;
      top: 307px;
      left: 168px;
      margin: 0;
      padding: 0;
      border: none;
      width: 136px;
      height: 32px;
      background: #FF3E79;
      border-radius: 16px;
      font-family: 'TTLakesMedium';
      font-size: 12px;
      line-height: 24px;
      text-align: left;
      padding-left: 20px;
      letter-spacing: -0.15px;
      text-transform: uppercase;
      color: #FFFFFF; }
      @media (min-width: 544px) {
        .modalCall .modalCall_form .modalCall_button {
          top: 226px;
          left: 272px; } }
  .modalCall .modalCall_text {
    margin: 0;
    margin-bottom: 18px;
    width: 288px;
    font-family: 'TTLakesRegular';
    font-size: 14px;
    line-height: 16px;
    color: #7E7E82; }
    @media (min-width: 544px) {
      .modalCall .modalCall_text {
        width: 376px; } }

.modalCall_title::before {
  content: " ";
  position: absolute;
  left: -16px;
  top: -4px;
  width: 4px;
  height: 40px;
  background: #41F6D7;
  border-top-right-radius: 45px;
  border-bottom-right-radius: 45px; }
  @media (min-width: 544px) {
    .modalCall_title::before {
      left: -32px; } }

.modalCall_text--colorText {
  color: #FF3E79; }

.modalCall_button::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 12px;
  left: 107px;
  height: 6px;
  width: 6px;
  border-top: 2px solid white;
  border-right: 2px solid white;
  border-top-left-radius: 1px;
  border-bottom-right-radius: 1px;
  transform: rotate(45deg); }
